<template>
    <div class="merchantList">
        <a-spin tip="加载中..." :spinning="spinning" size="large">
            <div style="text-align: center; margin-bottom: 8px">
                <a-icon type="tag" style="margin-right: 8px" />
                {{ `共查询到${pagination.total || 0}条数据` }}
                <span
                    @click="manualRefesh"
                    style="color: #409eff; margin-left: 10px; cursor: pointer"
                >
                    <a-icon type="sync" style="margin-right: 5px" />刷新
                </span>
                <a-checkbox
                    v-model="checked"
                    style="margin-left: 10px"
                    @change="onChange"
                    >显示</a-checkbox
                >
            </div>
            <a-table
                id="tableList"
                ref="tableList"
                bordered
                :columns="columns"
                :data-source="dataCollection"
                :scroll="{ y: 'calc(100vh - 360px)', x: 'scollXWidth' }"
                :pagination="pagination"
                :components='components'
            >
                <span
                    slot="subjectId"
                    slot-scope="text, record"
                    :title="`${record.subjectId}-${record.subjectName}`"
                    >{{ `${record.subjectId}-${record.subjectName}` }}</span
                >
                <template slot="autoMatch" slot-scope="text, record">
                    <!-- <a-button
                        type="primary"
                        shape="circle"
                        icon="eye"
                        size="small"
                        title="查看客商余额表"
                        class="del-match-btn"
                        @click="handleClickRow(record)"
                    /> -->
                    <a-popconfirm
                        title="确定要删除该条数据吗？"
                        ok-text="确 定"
                        cancel-text="取 消"
                        @confirm="deleteMatch(record)"
                    >
                        <a-button
                            type="primary"
                            shape="circle"
                            icon="delete"
                            size="small"
                            title="删除"
                            class="del-match-btn"
                        />
                    </a-popconfirm>
                </template>
                <template slot="footer">
                    <a-table
                        id="totalTable"
                        ref="totalTable"
                        :bordered="false"
                        :pagination="false"
                        :columns="columns"
                        :dataSource="footerTotalData"
                        :showHeader="false"
                        :scroll="{ y: 'calc(100vh - 360px)', x: 'scollXWidth' }"
                        row-key="id"
                    ></a-table>
                </template>
            </a-table>
        </a-spin>
        <a-modal
            v-model="searchDialogVisible"
            title="查询"
            ok-text="确认"
            cancel-text="取消"
            @ok="onSubmitSeachItem"
            @cancel="cancelSearch"
        >
            <a-form-model
                :model="searchForm"
                :label-col="labelCol"
                :wrapper-col="wrapperCol"
            >
                <a-form-model-item label="客商名称">
                    <a-input
                        v-model="searchForm.merchantName"
                        placeholder="请输入"
                    />
                </a-form-model-item>
                <a-form-model-item label="含抽凭数据">
                    <a-select
                        v-model="searchForm.isSampling"
                        placeholder="请选择"
                    >
                        <a-select-option
                            :value="item.value"
                            v-for="item in isSamplingList"
                            :key="item.key"
                        >
                            {{ item.label }}
                        </a-select-option>
                    </a-select>
                </a-form-model-item>
                <a-form-model-item label="科目">
                    <a-tree-select
                        v-model="searchForm.checkedKeys"
                        :tree-data="subjectTree"
                        placeholder="请选择科目"
                        allow-clear
                        show-search
                        key="key"
                        :replaceFields="{ value: 'subjectFullId' }"
                        :filterTreeNode="fillterFUN"
                    ></a-tree-select>
                </a-form-model-item>
            </a-form-model>
        </a-modal>
        <a-modal
            v-model="balanceDialogVisible"
            title="客商余额表"
            ok-text="确认"
            cancel-text="取消"
            @ok="balanceDialogVisible = false"
            @cancel="balanceDialogVisible = false"
            width="70%"
            class="show-content-dialog"
        >
            <a-table
                :columns="balanceColumns"
                :data-source="balanceDataCollection"
                :pagination="false"
                bordered
            />
        </a-modal>
    </div>
</template>
<script>
import XEUtils from 'xe-utils';
import { formatter } from '../libs/utils/formatter';
import session from '@data/session/sessionUtils.js';

import Vue from 'vue';
import VueDraggableResizable from 'vue-draggable-resizable';
Vue.component('vue-draggable-resizable', VueDraggableResizable);

export default {
    name: 'merchantList',
    computed: {
        relateSampling() {
            return this.$store.state.relateSampling;
        },
    },
    watch: {
        relateSampling: {
            handler(val) {
                if (val) this.handleRelateSampling();
            },
            immediate: true,
        },
    },
    data() {
             this.components = {
      header: {
        cell: (h, props, children) => {
          const { key, ...restProps } = props
        //   console.log('ResizeableTitle', key)
          const col = this.columns.find(col => {
            const k = col.dataIndex || col.key
            return k === key
          })
 
          if (!col || !col.width) {
            return h('th', { ...restProps }, [...children])
          }
 
          const dragProps = {
            key: col.dataIndex || col.key,
            class: 'table-draggable-handle',
            attrs: {
              w: 10,
              x: col.width,
              z: 1,
              axis: 'x',
              draggable: true,
              resizable: false
            },
            on: {
              dragging: (x, y) => {
                col.width = Math.max(x, 1)
              }
            }
          }
          const drag = h('vue-draggable-resizable', { ...dragProps })
          return h('th', { ...restProps, class: 'resize-table-th' }, [...children, drag])
        }
      }
    }
        return {
            columns: [
                {
                    title: '操作',
                    dataIndex: 'autoMatch',
                    width: 80,
                    scopedSlots: { customRender: 'autoMatch' },
                    fixed: 'left',
                },
                {
                    title: '客商编号',
                    dataIndex: 'merchantId',
                    ellipsis: true,
                    width: 150,
                },
                {
                    title: '客商名称',
                    dataIndex: 'merchantName',
                    ellipsis: true,
                    width: 220,
                },
                {
                    title: '含抽凭数据',
                    dataIndex: 'isSampling',
                    width: 100,
                    scopedSlots: { customRender: 'isSampling' },
                },
                {
                    title: '科目编号',
                    dataIndex: 'subjectId',
                    ellipsis: true,
                    width: 140,
                    scopedSlots: { customRender: 'subjectId' },
                },
                {
                    title: '期初余额',
                    dataIndex: 'remain',
                    align: 'right',
                    width: 170,
                    customRender: formatter.commafy,
                },
                {
                    title: '借方发生额',
                    dataIndex: 'debitOcc',
                    align: 'right',
                    width: 170,
                    customRender: formatter.commafy,
                },
                {
                    title: '贷方发生额',
                    dataIndex: 'creditOcc',
                    align: 'right',
                    width: 170,
                    customRender: formatter.commafy,
                },
                {
                    title: '期末余额',
                    dataIndex: 'balance',
                    align: 'right',
                    width: 170,
                    customRender: formatter.commafy,
                },
            ],
            dataCollection: [],
            spinning: false,
            isSamplingList: [
                { label: '全部', key: 0, value: '' },
                { label: '是', key: 1, value: '1' },
                { label: '否', key: 2, value: '0' },
            ],
            searchForm: {
                merchantName: null,
                isSampling: null,
                checkedKeys: '',
            },
            searchDialogVisible: false,
            labelCol: { span: 7 },
            wrapperCol: { span: 14 },
            queryParams: {
                sqlId: 'DG40003',
                start: 0,
                limit: 10,
            },
            balanceDialogVisible: false,
            balanceColumns: [
                {
                    title: '科目编号',
                    dataIndex: 'subjectId',
                    ellipsis: true,
                },
                {
                    title: '科目名称',
                    dataIndex: 'subjectName',
                    ellipsis: true,
                },
                {
                    title: '期初余额',
                    dataIndex: 'remain',
                    align: 'right',
                    customRender: formatter.commafy,
                },
                {
                    title: '借方发生额',
                    dataIndex: 'debitOcc',
                    align: 'right',
                    customRender: formatter.commafy,
                },
                {
                    title: '贷方发生额',
                    dataIndex: 'creditOcc',
                    align: 'right',
                    customRender: formatter.commafy,
                },
                {
                    title: '期末余额',
                    dataIndex: 'balance',
                    align: 'right',
                    customRender: formatter.commafy,
                },
            ],
            balanceDataCollection: [],
            pagination: {
                current: 1,
                pageSize: 10,
                showSizeChanger: true,
                pageSizeOptions: ['10', '20', '30', '40', '50'],
                onShowSizeChange: (current, pageSize) => {
                    this.queryParams.start = current - 1;
                    this.queryParams.limit = pageSize;
                    this.queryParams.isChange = true;
                    this.pagination.pageSize = pageSize;
                    this.queryMerchantListData();
                },
                showQuickJumper: false,
                size: 'small',
                total: 0,
                showTotal: () => `共${this.pagination.total}条`,
                change: true,
                onChange: (current, size) => {
                    this.queryParams.start = (current - 1) * size;
                    this.queryParams.limit = this.pagination.pageSize;
                    this.queryParams.isChange = false;
                    this.pagination.current = current;
                    this.queryMerchantListData();
                },
            },
            addDataDialogVisible: false,
            subjectTree: session.findMerchantRangeTree(),
            footerTotalData: [],
            checked: false,
        };
    },
    methods: {
        openSearchDialog() {
            this.searchDialogVisible = true;
        },
        handleClickRow(record, index) {
            if ((!record && !record.merchantName) || !record.merchantId) return;
            let params = {
                param1: record.merchantType,
                param2: record.merchantId,
            };
            this.spinning = true;
            this.$sacpApis
                .queryMerchantBalance(params)
                .then((res) => {
                    if (res.success) {
                        if (res.data && res.data.length)
                            res.data.forEach((item, index) => {
                                XEUtils.set(item, 'key', index);
                            });
                        this.balanceDataCollection = res.data;
                        this.balanceDialogVisible = true;
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => this.$message.error(err))
                .finally(() => (this.spinning = false));
        },
        onSubmitSeachItem() {
            this.queryParams.start = 0;
            this.queryParams.limit = 10;
            this.queryParams.isChange = true;
            XEUtils.set(
                this.queryParams,
                'param3',
                this.searchForm.merchantName
            );
            XEUtils.set(this.queryParams, 'param4', this.searchForm.isSampling);
            XEUtils.set(
                this.queryParams,
                'param7',
                this.searchForm.checkedKeys
            );
            this.queryMerchantListData();
        },
        cancelSearch() {
            this.searchDialogVisible = false;
            Object.keys(this.searchForm).forEach(
                (key) => (this.searchForm[key] = null)
            );
        },
        handleRelateSampling() {
            this.spinning = true;
            this.$sacpApis
                .relateSampling()
                .then((res) => {
                    if (res.success) {
                        this.$message.success('关联抽凭成功!');
                        this.queryMerchantListData();
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => {
                    this.$message.error(err);
                })
                .finally(() => {
                    this.spinning = false;
                });
        },
        queryMerchantListData() {
            this.spinning = true;
            this.queryParams.lockCustomerId =
                session.findUserInfo().curCustomerId;
            this.queryParams.lockProjectId =
                session.findUserInfo().curProjectId;
            this.queryParams.lockYyyy = session.findUserInfo().curAccYear;
             
            this.queryParams.param10 = this.checked ? '' : 1;
            this.$sacpApis
                .findMerchantList(this.queryParams)
                .then((res) => {
                    if (res.success) {
                        if (res.data && res.data.length)
                            res.data.forEach((item, index) => {
                                XEUtils.set(item, 'key', index);
                            });
                        this.dataCollection = res.data;
                        this.pagination.total = res.totalCount;
                        // 尾部合计
                        this.queryMerchantListTotalData();
                        if (this.queryParams.isChange)
                            this.pagination.current = 1; //分页回到第一页
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => {
                    this.$message.error(err);
                })
                .finally(() => {
                    if (this.searchDialogVisible) this.cancelSearch();
                    this.spinning = false;
                    this.isChange = false;
                });
        },
        uploadGetMatchtList() {
            //更新获取客商
            this.spinning = true;
            this.prohibitRowActive = false;
            this.$sacpApis
                .updateMerchantList()
                .then((res) => {
                    if (res.success) {
                        this.queryMerchantListData();
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => {
                    this.$message.error(err);
                })
                .finally(() => {
                    if (this.searchDialogVisible) this.cancelSearch();
                    this.spinning = false;
                });
        },
        deleteMatch(record) {
            const param = { param3: record.autoId };
            this.$sacpApis
                .deleteMerchantById(param)
                .then((res) => {
                    if (res.success) {
                        this.$message.success('删除成功!');
                        this.queryMerchantListData();
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => this.$message.error(err))
                .finally(() => {
                    this.spinning = false;
                });
        },
        fillterFUN(searchVal, treeNode) {
            return treeNode.data.props.title.includes(searchVal);
        },
        queryMerchantListTotalData() {
            this.spinning = true;
            const { curCustomerId, curProjectId ,curAccYear,} = session.findUserInfo();
            const param = JSON.parse(JSON.stringify(this.queryParams));
            param.lockCustomerId = curCustomerId;
            param.lockYyyy = curAccYear,
            param.lockProjectId = curProjectId;
            param.sqlId = 'DG40056';
            param.refreshFlg = 1;
            param.limit = 1;
            delete param.start;
            this.$sacpApis
                .findMerchantList(param)
                .then((res) => {
                    if (res.success) {
                        if (res.data && res.data.length) {
                            const { balance, creditOcc, debitOcc, remain } =
                                res.data[0];
                            this.footerTotalData = [
                                {
                                    id: 1,
                                    balance,
                                    creditOcc,
                                    debitOcc,
                                    remain,
                                    autoMatch: '合计',
                                    merchantId: '',
                                    merchantName: '',
                                    isSampling: '',
                                    subjectId: '',
                                },
                            ];
                        } else {
                            this.footerTotalData = [
                                {
                                    id: 1,
                                    balance: '',
                                    creditOcc: '',
                                    debitOcc: '',
                                    remain: '',
                                    autoMatch: '合计',
                                    merchantId: '',
                                    merchantName: '',
                                    isSampling: '',
                                    subjectId: '',
                                },
                            ];
                        }
                        // console.log('res.data', res.data);
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => {
                    this.$message.error(err);
                })
                .finally(() => {});
        },
        onScroll(val) {
            // 同步表与footer滚动
            const dome =
                this.$refs.totalTable.$el.querySelectorAll('.ant-table-body');
            const dome1 =
                this.$refs.tableList.$el.querySelectorAll('.ant-table-body');
            dome[0].addEventListener(
                'scroll',
                () => {
                    dome1[0].scrollLeft = dome[0].scrollLeft;
                },
                true
            );
        },
        manualRefesh() {
            this.queryMerchantListData();
        },
        onChange() {
            this.queryMerchantListData();
        },
    },
    mounted() {
        this.onScroll();
    },
    created() {
        this.queryMerchantListData();
    },
};
</script>

<style lang="scss" scoped>
/deep/#tableList {
    .ant-table-body {
        overflow-x: hidden !important;
    }
    .ant-table-content {
        table {
            border-collapse: collapse !important;
            .ant-table-thead {
                [key='autoMatch'] {
                    border-right: 0px;
                }
                [key='merchantId'] {
                    border-left: 1px solid #e8e8e8;
                }
            }
        }
        .ant-table-fixed-left {
            .ant-table-fixed {
                .ant-table-tbody {
                    tr {
                        td {
                            border-right: 0px;
                        }
                    }
                }
            }
        }
    }
    .ant-table-footer {
        padding: 0 !important;
        #totalTable {
            .ant-table-body {
                overflow: auto !important;
                .ant-table-fixed {
                    .ant-table-row-cell-break-word {
                        //ant-table-fixed-columns-in-body
                        // border: 0;
                        color: black;
                    }
                }
            }
            .ant-table-content {
                .ant-table-fixed-left {
                    display: none;
                }
            }
        }
    }
}
.resize-table-th {
  position: relative;
}
.table-draggable-handle {
  transform: none;
  position: absolute;
  height: 100% !important;
  left: auto !important;
  right: -5px;
  cursor: col-resize;
  touch-action: none;
  border: none;
}
</style>


